@import "../../App.scss";

.form,
.booking-date,
.check-in,
.booking-details {
    h1 {
        color: var(--color-2);

        @include breakpoints(sm) {
            font-size: 20px;
        }
    }
}

// "Completá tus datos"
.title-form {
    background-color: rgba(243, 241, 237, 0.415);
    padding: 40px 0px 16px 40px;
    color: var(--color-2);

    @include breakpoints(sm) {
        font-size: 20px;
        padding: 30px 13px 0px 13px;
    }
}

.booking-information {
    padding: 0px 40px 40px 40px;
    width: 100%;
    max-width: 100vw;
    max-height: 100%;
    background-color: rgba(243, 241, 237, 0.415);
    display: flex;
    justify-content: space-between;

    @include breakpoints(md) {
        flex-direction: column;
    }

    @include breakpoints(sm) {
        flex-direction: column;
        padding: 16px 13px 40px 13px;
    }
}

/************ FORMULARIO DE RESERVA ************/
.form {
    width: 72%;
    @include breakpoints(md) {
        width: 100%;
    }
    @include breakpoints(sm) {
        width: 100%;
    }
    padding-right: 40px;

    h1 {
        padding-bottom: 1rem;
    }

    @include breakpoints(md) {
        padding-right: 0;
    }

    @include breakpoints(sm) {
        padding-right: 0;
    }

    /* BLOQUE DE INPUTS  */
    .form-inputs {
        @include breakpoints(sm) {
            flex-direction: column;
        }

        .card-booking {
            display: flex;
            padding: 20px 13px;

            @include breakpoints(sm) {
                flex-direction: column;
                padding: 0px;
            }
        }

        .inputs-div {
            display: flex;
            flex-direction: column;
            width: 50%;
            padding: 0px 15px;

            @include breakpoints(sm) {
                width: 100%;
                // padding: 0px 15px 0px 15px;
            }

            label {
                font-size: 14px;
                color: var(--color-2);
                font-style: bold;
                margin-top: 20px;
                margin-bottom: 10px;
                align-self: flex-start;
                font-weight: 700;
                line-height: 14px;
            }

            input {
                width: 100%;
                height: 3rem;
                border-radius: 5px;
                border: 1px solid #fff;
                padding: 0.5rem;
                margin-bottom: 1rem;
                box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.15);

                &:focus {
                    outline: none;
                }

                &:active {
                    outline: none;
                }
            }
        }
    }

