/*********** DETALLE DE LA RESERVA *************/

.booking-details {
    width: 28%;

    @include breakpoints(md) {
        width: 100%;
    }
    @include breakpoints(sm) {
        width: 100%;
    }
    .card-booking {
        min-height: 100%;

        @include breakpoints(md) {
            display: flex;
            flex-direction: column;
        }

        .content-booking-details {
            @include breakpoints(md) {
                display: flex;
                justify-content: space-between;
                padding: 0px 25px 25px 25px;
            }

            .image-container {
                // width: 481px;
                height: 300px;
                margin-bottom: 30px;

                @include breakpoints(md) {
                    width: 50%;
                    height: auto;
                    margin-bottom: 0px;
                }

                @include breakpoints(sm) {
                    min-width: 100%;
                    margin-bottom: 0px;
                }

                .image {
                    width: 100%;
                    height: 100%;
                    background-size: cover;
                    background-position: center;
                    background-repeat: no-repeat;

                    @include breakpoints(md) {
                        border-radius: 10px;
                    }

                    @include breakpoints(sm) {
                        width: 100%;
                        height: 100%;
                    }
                }
            }
        }

        .bottom-booking-details {
          
            h4 {
                color: var(--gris-plomo);
            }

            @include breakpoints(md) {
                width: 50%;
                padding: 0px 0px 0px 34px;
            }

            .location-booking-details {
                display: inline-flex;
                align-items: center;
                padding: 18px 0px 30px;

                @include breakpoints(md) {
                    padding: 18px 0px 15px;
                }

                h4,
                svg {
                    color: var(--color-2);
                }
            }

            .check-in-booking-details {
                margin-top: 25px;

                @include breakpoints(md) {
                    margin-top: 0px;
                }

                @include breakpoints(sm) {
                    margin-top: 0px;
                }
                h4 {
                    // padding: 20px 0px 20px;
                    padding: 33px 0px 33px;
                    @include breakpoints(md) {
                        padding:20px 0px 20px ;
                    }
                    @include breakpoints(sm) {
                        padding: 20px 0px 20px ;
                    }
                }

                .check-in-check-out {
                    width: 100%;
                    display: inline-flex;
                    justify-content: space-between;
                }
            }

            .hr-booking {
                border: 1px solid #bebebe;
            }

            button {
                // margin-top: 3px;
                color: var(--blanco);
                background-color: var(--color-principal);
                border: 1px solid var(--color-principal);
                padding: 15px;
                margin-top: 75px;
                font-family: "Roboto", cursive;
                font-size: 16px;
                font-weight: 600;
                width: 100%;
                border-radius: 5px;
                box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.12);
                cursor: pointer;

                @include breakpoints(md) {
                    margin-top: 25px;
                }
                @include breakpoints(sm) {
                    margin-top: 30px;
                }
            }
        }
    }

    .bottom-booking-details,
    .title-booking-details {
        padding: 24px;

        @include breakpoints(sm) {
            padding: 16px;
        }
    }
}

/************SUCCESS*************/
.booking-alternative {
    height: 100vh;
    text-align: center;
}

.booking-success-container {
    width: 100%;
    padding: 40px;
    background-color: var(--color-4);
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;

    @include breakpoints(sm) {
        padding: 10px;
    }
}

.booking-success-card {
    width: 55%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 15%;
    margin-bottom: 15%;
    padding: 2em;
    max-height: 350px;
    max-width: 750px;

    @include breakpoints(md) {
        width: 90%;
    }

    @include breakpoints(sm) {
        min-width: 350px;
        padding: 1em;
    }

    img {
        width: 128px;

        @include breakpoints(sm) {
            width: 96px;
        }
    }

    h1 {
        margin-top: 20px;
        margin-bottom: 10px;
        color: var(--color-principal);
    }

    h3 {
        color: var(--color-2);
    }

    a {
        background-color: var(--color-principal);
        border-color: transparent;
        width: 40%;
        margin-top: 1em;
        border-radius: 0.5em;
        height: 3.2em;
        font-size: inherit;
        font-weight: bold;
        display: flex;
        justify-content: center;
        align-items: center;
        color: white;

        @include breakpoints(sm) {
            width: 80%;
        }

        a {
            color: white;
            font-size: large;
            font-weight: bold;
        }
    }
}

