/*BODY*/

.body-container {

    width: 100%;
    min-height: 100vh;
    padding-top: 67px;
    margin: auto;
}

/*SEEKER*/

.seeker-container {
    background-color: #545776;
    padding: 0 7em 0 7em;
}

.seeker-container h1 {
    padding-top: 0.8em;
    text-align: center;
    color: #fff;
    font-size: 36px;
    margin: 1.5rem;
}

.css-b62m3t-container {
    width: 400px;
    text-align: left;
    flex-direction: column;
}






.seeker-select span {
    display: none;
}

.seeker-item {
    margin: 0 1%;


}

/* .seeker-picker{
    width: 40%;
   
} */

.input-level {
    font-size: medium;
    color: hsl(0, 0%, 50%);
    padding: revert;
    height: 3em;
    border-radius: 0.3em;
    border-color: transparent;
    width: 90%;
    margin: auto;
}


.seeker-select {
    height: 5rem;
    margin: auto;
    display: flex;
    max-width: 1024px;

}

.seeker-select span {
    display: none;

}

.seeker-button {
    color: #fff;
    background-color: #1DBEB4;
    border-color: transparent;
    cursor: pointer;
    font-size: medium;
    font-weight: bold;
    border-radius: 0.3em;
    width: 200px;
    height: 3em;
}

.css-1s2u09g-control {
    height: 3em;
    border-radius: 0.6em;
    width: 98%;
    align-content: center;
    margin: auto;
    display: block;

}



.css-1hb7zxy-IndicatorsContainer {
    display: none !important;
}

.css-319lph-ValueContainer {
    height: 3em;
}

.react-datepicker__close-icon {}

.react-datepicker__close-icon::after {

    background-color: #ffffff;
    color: #545776;

    font-size: 12px;
    font-weight: bolder;
    content: "×";
}

.react-datepicker__day--keyboard-selected {
    background-color: #545776;
}

.react-datepicker__day--in-range {
    background-color: #545776;
}


.react-datepicker__tab-loop {
    display: flex;
    justify-content: center;
}


.grider-container {
    margin-top: 2em;
    color: #545776;
    display: flex;
    flex-direction: column;
    max-width: 1440px;
    margin-inline: auto;

}

.grider-container h2 {
    padding-left: 7em;
}

.grider-card {
    margin-top: 2em;
    display: flex;
    justify-content: center;
    padding: 0 3em 0 3em;

}

.grider-card-item {
    display: flex;
    flex-direction: column;

    margin: 0 1em;
    box-shadow: -1px 3px 10px #74757e6b;
    border-radius: 30px;
    cursor: default;


    max-height: 300px;
    max-width: 328px;
}

.grider-card-item img {
    border-radius: 30px 30px 0 0;
    cursor: pointer;
    min-height: 200px;
    max-height: 200px;
    object-fit: cover;
}

.grider-card-details {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0.5em 1em;
}

.listing-container {
    margin-top: 2em;
    background-color: hsla(235, 40%, 75%, 0.212);
    display: flex;
    flex-direction: column;
}

.listing-container ul {
    color: #545776;
    display: flex;
    flex-flow: wrap;
    justify-content: center;
    margin-bottom: 2em;
}

.listing-container h1 {

    text-align: left;
    color: #545776;
    font-size: 1.8rem;
    font-weight: bold;
    margin: 2.5rem;
    margin-left: 3em;

}

.listing-card {
    background-color: #fff;
    margin: 1em 2em;
    width: 45%;
    border-radius: 1em;
    box-shadow: 0px 3px 10px #74757e6b;
    display: inline-flex;
    min-height: 290px;
    max-width: 650px;

}

.listing-card p {
    font-size: 1.6ex;
    display: inline-flex;
}

.listing-card h5 {
    font-size: medium;
    font-weight: 400;
}

.icons-amenities {
    font-size: x-large !important;
    display: flex;
}

.icon-img {
    font-size: larger !important;
    padding: 1% 20% 0;
    position: absolute;

}

.listing-card-details h3 {
    text-transform: uppercase;
}

.listing-card-details h3 span {
    font-size: 1.2rem;
    color: #1DBEB4;
}

.review-continer {
    width: 30%;
    display: flex;
    flex-flow: inherit;
    justify-content: end;
}

.review-number {

    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-size: larger !important;
    width: 2.6em;
    height: 2em;
    background-color: #545776;
    box-shadow: 0px 3px 10px #74757e6b;
    border-radius: 0.4em;
}

.review-text {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: medium;
    margin-top: 0.3em;
}

a {
    text-decoration: none;
    font-weight: 200;
    color: #1DBEB4;
}


.listing-card img {
    width: 50%;
    border-radius: 1em;
    display: flex;
    flex-direction: column;
}

.listing-card-details {
    display: flex;

    padding: 0.5em;

    flex-flow: wrap;

}

.icons {
    color: #545776;
}

.stars-icons {
    color: #1dbeb4;
}

.listing-card-header {
    width: 70%;
    display: flex;
    flex-direction: column;
}

.review-container {
    width: 30%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.listing-card-button {
    color: #fff !important;
    background-color: #1DBEB4;
    border-color: transparent;
    width: 100%;
    margin-top: 1em;
    position: relative;
    padding: 10px;
    border-radius: 0.5em;
    margin: auto;
    cursor: pointer;
    height: 3em;
    font-size: inherit;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
}


/******************************************/

@media screen and (max-width: 780px) {

    .seeker-container {

        padding: 0 3em 0 3em;
    }

    .seeker-container {

        padding: 0 3em 0 3em;
    }

    .css-1s2u09g-control {
        width: 98%;
        height: 3em;
    }

    .grider-card {

        flex-direction: row;
        align-items: center;
        flex-wrap: wrap;
    }

    .listing-card {

        width: 100%;

    }

    .seeker-select {
        width: initial;
    }

    .seeker-picker {
        width: initial;
    }

    .input-level {
        width: initial
    }

    .seeker-button {
        width: 90%;
    }

    .grider-card-item {

        width: 18em;
        margin: 0.5em 1em;
        max-height: 285px;

    }

    .grider-container {
        padding: 0 3em 0 3em;
    }

    .icon-img {
        padding: 1% 40% 0;
    }

    .review-container {
        display: flex;
        flex-flow: column;
        align-items: end;

    }

    .icons-amenities {
        font-size: x-large !important;
        display: inline-block;
    }

}



/******************************************/

@media screen and (max-width: 480px) {


    .seeker-container {

        box-shadow: inset 0px 0 10px rgba(0, 0, 0, 0.3);
        padding: initial;
        display: flex;
        height: 310px;

    }

    .seeker-container h1 {

        font-size: 2rem;

    }

    .seeker-item {
        display: flex;
        align-items: center;
        flex-direction: column;
        padding: inherit;
        margin-top: 10px;
    }



    .css-b62m3t-container {

        width: 98%;
        text-align: left;
    }

    .css-14el2xx-placeholder {
        color: #383B58 !important;
        font-weight: 500;
    }

    .seeker-item {

        display: flex;
        align-items: center;
        flex-direction: column;
        padding: inherit;
    }

    .inputCalendar {
        width: 30em;
        border-radius: 4px;
    }

    .seeker-button {
        color: #fff;
        background-color: #1DBEB4;
        border-color: transparent;
        cursor: pointer;
        width: -moz-available;
        font-size: medium;
        border-radius: 0.3em;
        margin-top: 15px;
    }

    .grider-container {

        color: #545776;
        margin-top: 2em;
        display: flex;
        flex-direction: column;
        color: #545776;
        padding: 0 3em 0 3em;

    }

    .seeker-picker {
        width: 98%;
        height: auto;
    }


    .seeker-container {

        height: 360px;
    }

    .seeker-button {
        height: 3em;
        width: 98%;
        justify-content: center;
    }

    .input-level {

        width: 97%;

    }

    .seeker-select {
        margin: initial;
        flex-flow: wrap;
    }

    .listing-card {
        display: block ruby;

        width: 90%;
    }

    .listing-card-details {

        width: initial;


    }

    .listing-card img {
        width: 100%;
        border-radius: 1em 1em 0 0;

    }



    .grider-card {
        margin-top: 2em;
        display: flex;

        justify-content: center;
        margin-right: 2rem;
    }

    .grider-card-item {
        display: flex;
        flex-direction: column;
        width: 21rem;
        box-shadow: 0px 3px 10px #74757e6b;
        border-radius: 6%;
        margin-top: inherit;
        max-height: initial;
    }

    .grider-container h2 {
        font-size: x-large;
        text-align: center;
    }


    .listing-card {

        display: inline-block;
    }


    .listing-card-header {
        width: 75%;
        display: flex;
        flex-direction: column;

    }

    .listing-card-button {
        color: #fff !important;
        background-color: #1DBEB4;
        border-color: transparent;


    }

    .listing-container h1 {
        text-align: center;
        margin-left: initial;
    }

    .review-container {
        width: 25%;
    }


    .listing-card .icon-img {
        font-size: larger !important;
        position: relative;
        top: -14em;
        left: 11em;

    }

    .icons-amenities {
        display: none;
    }


}